import React from 'react'
import {Modal, Button} from "antd"
import { CloseOutlined } from '@ant-design/icons';
import './ModalInformativo.css'
import {
    abrirCerrarModalInformativoPromociones} from 'appRedux/actions/Promociones'
import {useDispatch, useSelector} from "react-redux";
import config from 'config'

const ModalInformativo = () => {

    const dispatch = useDispatch();
    const {mostrarModalInformativo} = useSelector(({promociones}) => promociones);


    return (
        <Modal
            width={850}
            height = {406}
            title={null}
            visible={mostrarModalInformativo}
            footer = {null}
            closeIcon = {
                <Button
                    style={{background:'#30BEEB', border:'none', marginTop:'10px', marginRight:'5px'}}
                    shape="circle" 
                    icon={<CloseOutlined style={{"color" : 'white'}} />} 
                    onClick={() => dispatch(abrirCerrarModalInformativoPromociones(!mostrarModalInformativo))}
                >
                </Button>
            }
            bodyStyle   = {{backgroundImage: `url(${config.api}Sistema/abs/img/importantev2.png)`, backgroundSize: '100% 100%', backgroundRepeat:'no-repeat', borderRadius:'20px'}}
        >
            <div id="contenedorModal">
                <h1 id="txt_tituloModal">IMPORTANTE</h1><br/>
                <div id="txt_contenidoModal">
                    Estimado Cliente,<br/><br/>
                    Les informamos que a travs de este portal se les comunicar de manera mensual las tcticas promocionales con el nmero de combos e inversin dineraria que ha sido aprobada por actividad y monto mximo para cada mecnica. Dichos montos,  no podrn ser excedidos de no recibir una aprobacin formal del rea de Trade y posterior actualizacin en este portal; . Bajo ningn supuesto o circunstancia, el ejecutivo comercial podr ofrecer y mucho menos aprobar acciones adicionales<br/><br/>
                    Finalmente, les recordamos que los pagos se realizarn a travs de notas de crdito, las mismas que ser aprobadas una vez compartido el cierre de sus ventas hasta el quinto da hbil del siguiente mes y pagadas el siguiente mes. el cierre de ventas no es enviado dentro del plazo establecido, Kimberly- Clark Per se encontrar facultado postergar el pago hasta dos meses posteriores a la ejecucin de  las tcticas promocionales.<br/><br/>
                    Saludos,
                </div>
            </div>
        </Modal>
    )
}

export default ModalInformativo
